// COMPONENT - PROFILE PAGE
const ProfilePage = ({ setPage,mainPage,setMainPage }) => {
	const { sso }  = useSelector(state => state);
	const { user,evsdata } = sso;
	const dispatch = useDispatch()
    const { setting } = useSelector( state => state.theme )
	const { REACT_APP_THEME_TAG : themetag } = process.env; 
	const ui = setting.clients.find( r => r.tag === themetag )
    const { REACT_APP_API_URL : API_URL } = process.env; 
	const [ Logo,setLogo ] = useState(null)
	//const [ mainpage,setMainPage ] = useState('candidate')

	const PageRenderer = () => {
	   switch(mainPage){
		 case 'control' : return <ControlPage mainPage={mainPage}/>; break;
		 case 'elector' : return <ElectorPage mainPage={mainPage}/>; break;
		 case 'portfolio' : return <PortfolioPage mainPage={mainPage}/>; break;
		 case 'candidate' : return <CandidatePage mainPage={mainPage}/>; break;
		 default: return <ControlPage />
	   }
	}
	
	useEffect(() => {
		import(`../../../assets/img/logo/${themetag}/logo.png`).then(src => setLogo(src.default))
	},[])

	return <PageRenderer/>
	   
}


const ControlPage = ({ mainPage }) => {
	
	const dispatch = useDispatch()
    const { sso }  = useSelector(state => state);
	const { evsdata } = sso;
	const [ election, setElection ] = useState({})
	const [ variable,setVariable ] = useState({key:'',value:''})
	const [ action, setAction ] = useState(null)
	const memoForm = useMemo(() => form,[form])

	const [ form, setForm ] = useState({
		allow_monitor: 0,
		allow_vip : 0,
		allow_monitor_ec: 0,
		allow_vip_ec : 0,
		allow_result_ec : 0,
		allow_site : 0,
		allow_result: 0,
		allow_vmask: 0,
        status: 'STAGED'
	})

	const getElectionInfo = async () => {
        const { id } = evsdata
		const resp = await fetchEvsMonitor(id);
	    if(resp.success){
		  const result = resp.data.election && resp.data.election[0];
		  setElection(result)
		  setForm({
			allow_monitor : result.allow_monitor,
			allow_vip : result.allow_vip,
			allow_monitor_ec : result.allow_monitor_ec,
			allow_vip_ec : result.allow_vip_ec,
			allow_result_ec : result.allow_result_ec,
			allow_site : result.allow_site,
			allow_result: result.allow_result,
			allow_vmask: result.allow_vmask,
			status: result.status
		  })
		}
    }

	const getMessage = (field,action) => {
		var msg = ``;
		switch(field){
			case 'allow_monitor': msg = 'Public monitor '; break
			case 'allow_vip': msg = 'VIP Strongroom '; break
			case 'allow_monitor_ec': msg = 'EC\'s Public monitor '; break
			case 'allow_vip_ec': msg = 'EC\'s VIP Stroongroom '; break
			case 'allow_result_ec': msg = 'EC\'s Election result viewing '; break
			case 'allow_result': msg = 'Election result viewing '; break
			case 'allow_site': msg = 'Election group\'s landing page '; break
			case 'allow_vmask': msg = 'Voters statuses '; break
			case 'status': msg = 'Election process\'s landing page '; break
		}

		switch(action){
			case 1: msg += 'enabled '; break
			case 0: msg += 'disabled '; break
			case 'STARTED': msg += 'started ! '; break
			case 'HALTED': msg += 'halted ! '; break
			case 'ENDED': msg += 'has ended ! '; break
			case 'STAGED': msg += 'has been staged! '; break
		}
		return msg;

	}

	const onEvsChange = async (e) => {
		setForm({ ...form,[e.target.name] : e.target.value })
		setVariable({ key:e.target.name, value:e.target.value })
		setAction(e.target.name)
	}

	const onEvsClick = (key,value) => {
		setForm({ ...form,[key]:value })
		setVariable({ key, value })
		setAction(key)
	}

	const submit = async () => {
		const { id } = evsdata
		const { key,value } = variable;
		const data = { data:{...form },id }
		const resp = await updateEvsControlData(data);
		if(resp.success){
		  setAction(null);
		  //const msg = getMessage(key,value)
		  //dispatch(updateAlert({ show:true, message:msg && msg.toUpperCase(), type:'info' }))
	    }
	}

	
	useEffect(() => {
	   getElectionInfo()
	},[])

	useEffect(() => {
	  action && submit();
	},[form])

	return <>
	  <div className="content-area card">
		<div className="card-innr">
				<div className="token-statistics card card-token height-auto">
					<div className="card-innr">
						<div className="token-balance">
							<div className="token-balance-text">
								<span className="lead text-white font-weight-bold">{mainPage && mainPage.toUpperCase()+'S'}</span>
							</div>
						</div>
					</div>
				</div>
			
			{/* CONTENT HERE */}
			<div className="content-area card px-2 bg-white portfolio_cover">
				
				<span className={`portfolio`}>
					
					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>ELECTION PROCESS</b>
							   <select className="select" style={{ width: '100px', height:'35px', fontweight:'bolder' }} name="status" onChange={onEvsChange} >
								   <option value="STAGED" selected={form.status == 'STAGED'}>STAGED</option>
								   <option value="STARTED" selected={form.status == 'STARTED'}>STARTED</option>
								   <option value="HALTED" selected={form.status == 'HALTED'}>HALTED</option>
								   <option value="ENDED" selected={form.status == 'ENDED'}>ENDED</option>
							   </select>
							</div>	
						</div>
					</div> 
					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>PUBLIC MONITOR</b>
							   <img onClick={(e) => onEvsClick('allow_monitor',form.allow_monitor == 0 ? 1:0)} src={form.allow_monitor == 0 ? Off : On } style={{ height: '40px', cursor:'pointer'}} />
							</div>	
						</div>
					</div> 
					
					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>VIP MONITOR</b>
							   <img onClick={() => onEvsClick('allow_vip',form.allow_vip == 0 ? 1:0)} src={form.allow_vip == 0 ? Off : On } style={{ height: '40px', cursor:'pointer'}} />
							</div>	
						</div>
					</div> 

					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>RESULTS VIEW</b>
							   <img onClick={() => onEvsClick('allow_result',form.allow_result == 0 ? 1:0)} src={form.allow_result == 0 ? Off : On } style={{ height: '40px', cursor:'pointer'}} />
							</div>	
						</div>
					</div>  

					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>EC PUBLIC MONITOR</b>
							   <img onClick={() => onEvsClick('allow_monitor_ec',form.allow_monitor_ec == 0 ? 1:0)} src={form.allow_monitor_ec == 0 ? Off : On } style={{ height: '40px', cursor:'pointer'}} />
							</div>	
						</div>
					</div> 

					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>EC VIP MONITOR</b>
							   <img onClick={() => onEvsClick('allow_vip_ec',form.allow_vip_ec == 0 ? 1:0)} src={form.allow_vip_ec == 0 ? Off : On } style={{ height: '40px', cursor:'pointer'}} />
							</div>	
						</div>
					</div> 

					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>EC RESULT VIEW</b>
							   <img onClick={() => onEvsClick('allow_result_ec',form.allow_result_ec == 0 ? 1:0)} src={form.allow_result_ec == 0 ? Off : On } style={{ height: '40px', cursor:'pointer'}} />
							</div>	
						</div>
					</div> 

					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>VOTER STATUS</b>
							   <img onClick={() => onEvsClick('allow_vmask',form.allow_vmask == 0 ? 1:0)} src={form.allow_vmask == 0 ? Off : On } style={{ height: '40px', cursor:'pointer' }} />
							</div>	
						</div>
					</div> 
					
					<div className="candidates py-2 px-0">
						<div className="candbio">
						   <div style={{ width: '100%', display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>WEBSITE VIEW</b>
							   <img onClick={() => onEvsClick('allow_site',form.allow_site == 0 ? 1:0)} src={form.allow_site == 0 ? Off : On } style={{ height: '40px', cursor:'pointer' }} />
							</div>	
						</div>
					</div> 

				</span>
			  

			</div>
		</div>
	  </div>
	</>
	
}


const ElectorPage = ({ mainPage }) => {
    
	const { sso }  = useSelector(state => state);
	const { evsdata } = sso;
	const [ elector,setElector ] = useState([])
    
	const getElectors = async () => {
		const { id } = evsdata
		const resp = await fetchEvsRegister(id);
		if(resp.success){
		  setElector([...resp.data.electors]);
		}
	}

	const removeElector = async (tag) => {
		const resp = await deleteElector(tag);
		console.log(resp)
		if(resp.success){
			getElectors();
		}
	}

	useEffect(() => {
		getElectors();
	},[])

	return <>
	  <div className="content-area card">
		<div className="card-innr">
				<div className="token-statistics card card-token height-auto">
					<div className="card-innr">
						<div className="token-balance">
							<div className="token-balance-text">
								<span className="lead text-white font-weight-bold">{mainPage && mainPage.toUpperCase()+'S'}</span>
							</div>
						</div>
					</div>
				</div>
			
			{/* CONTENT HERE */}
			<div className="content-area card px-2 bg-white portfolio_cover">
				
				<div>
					<div className="card-head">
						<form className="">
							<input type="text" placeholder="ENTER UNIQUE TAG" />
							<button>ADD</button>
						</form>
					</div>
				</div>

				<span className={`portfolio`}>
					{ elector && elector.map( row => (
					<div className="candidates py-2" key={row.tag}>
						<div className="candbio">
						    <img src={`/photos/?tag=15666`} alt="Voter" loading="lazy" style={{borderRadius:'5px', height:'40px'}}/>
							<div>
							   <span>{row.name}</span>
							   <br/><b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>{row.mail}</b>
							   <b className={`badge badge-outline badge-dark btn-sm font-weight-bold`}>{row.tag}</b>
							   <b className={`badge badge-outline badge-success btn-sm font-weight-bold d-none`}><i className="fa fa-edit text-success"></i></b>
							   <b className={`badge badge-outline badge-danger btn-sm font-weight-bold`} onClick={() => removeElector(row.rag)}><i className="fa fa-trash text-danger"></i></b>
							</div>	
						</div>
					</div> 
					))}
					
				</span>
				
			  

			</div>
		</div>
	</div>
	</>
}


